const GridHelper = {
    baseOnGridReady: (params) => {
        let rowHeight = params.api.gridOptionsWrapper.gridOptions.rowHeight,
            headerHeight = 26,
            scrollbarHeight = 0;

        params.api.gridCore.eGridDiv.style.height = (scrollbarHeight + headerHeight + rowHeight * (params.api.rowModel.rowsToDisplay.length > 10 ? 10 : params.api.rowModel.rowsToDisplay.length)) + 'px';

        console.log('setting height to', params.api.gridCore.eGridDiv.style.height, 'cause', params.api.rowModel.rowsToDisplay.length > 10 ? 10 : params.api.rowModel.rowsToDisplay.length,
            'times', rowHeight, '=', scrollbarHeight + headerHeight + rowHeight * (params.api.rowModel.rowsToDisplay.length > 10 ? 10 : params.api.rowModel.rowsToDisplay.length));

        setTimeout(() => params.api.sizeColumnsToFit(), 150);
    }
}

export default GridHelper;


